import React from 'react';
import styled from 'styled-components';
import { Button } from "../../components/ui";
import SubPageWrapper from '../SubPageWrapper';

interface Project {
  navKey: string;
  title: string;
  sub: string[];
  link1: string;
  link1Label: string;
  link2: string;
  link2Label: string;
  img1: string;
  img2: string;
}


export default ({ navKey, title, sub, link1, link1Label, link2, link2Label, img1, img2 }: Project) => {
  return (
    <SubPageWrapper navKey={navKey} img={img1} >
      <Wrapper>
        <div>
          <Title>{title}</Title>
          <Sub>
            {sub.map((v, i) => (
              <React.Fragment key={i}>
                {v}
                {i !== sub.length - 1 && <br />}
              </React.Fragment>
            ))}
          </Sub>
          <div>
            <Button href={link1}>{link1Label}</Button>
            <Button href={link2}>{link2Label}</Button>
          </div>
        </div>
        <Img src={img2} alt="band" />
      </Wrapper>
      <Sp />
    </SubPageWrapper>
  );
}

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;

  @media only screen and (min-width: 1620px) {
    flex-direction: row;
  }
`;

const Title = styled.div`
  font-size: 20pt;
  font-family: "JSDongkang-Bold";
  margin-bottom: 20px;
`;

const Sub = styled.div`
  font-size: 18pt;
  margin-bottom: 20px;

  @media only screen and (min-width: 1280px) {
    font-size: 16pt;
  }
`;

const Img = styled.img`
  height: 270px;
  width: 480px;
  margin-bottom: 50px;

  @media only screen and (min-width: 1620px) {
    margin-left: auto;
    margin-right: 0;
  }
`;

const Sp = styled.div`
  border-bottom: 1px solid #a6a6a6;

  @media only screen and (min-width: 1280px) {
    margin: 0 50px;
  }
`;