import React, { useState } from 'react';
import styled from 'styled-components';
import LanguageStyled from "../Language";
import { useLocation, Link } from 'react-router-dom';
import { navArr, InavInsideData } from '../../shared/route';
import NavInside from './inside';

const Logo = styled.img.attrs({
  src: "/static/images/logo.png",
  alt: "logo",
})`
  margin: 10px 0;
  height: 50px;
  @media only screen and (min-width: 1280px) {
    margin: 20px 0;
    height: 60px;
    margin-right: 40px;
  }
`;

interface NavItemPropsWithEvent extends InavInsideData {
  onMouseEnter(): void;
}

const NavItem = (props: NavItemPropsWithEvent) => {
  return (
    <NavItemStyle onMouseEnter={props.onMouseEnter}>{props.title}</NavItemStyle>
  );
}


export default () => {
  const [scrollY, setScrollY] = useState(0);
  const [hover, setHover] = useState(false);
  const [menu, setMenu] = useState("");
  const location = useLocation();

  window.addEventListener('scroll', () => setScrollY(window.scrollY))


  const navMenuMoveEvent = (props: InavInsideData) => {
    setHover(true);
    setMenu(props.key)
  }

  return (
    <>
      <NavMenu opacity={scrollY < 300 && !hover && location.pathname === "/" ? 0.3 : 1} onMouseLeave={() => { setHover(false); }}>
        <div>
          <NavButton onClick={() => setHover(!hover)} />
          <Link to="/">
            <Logo />
          </Link>
          {navArr.map((i) => (
            <NavItem key={i.key} {...i} onMouseEnter={() => navMenuMoveEvent(i)} />
          ))}
          <LanguageStyled />
        </div>
        <NavInside visible={hover} menu={menu} handler={setHover} />
      </NavMenu>
      <Sp opacity={scrollY < 300 && !hover && location.pathname === "/" ? 0 : 1} />
    </>
  )
}

interface NavProps {
  opacity: number;
}

const NavMenu = styled.nav<NavProps>`
  transition: background .3s ease-in-out;
  height: 70px;
  background: white;
  
  @media only screen and (min-width: 1280px) {
    height: 100px;
    background: rgba(255, 255, 255, ${(props: NavProps) => props.opacity});
  }

  & > div:nth-child(1) {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media only screen and (min-width: 1280px) {
      padding: 0 50px;
    }

    @media only screen and (min-width: 1620px) {
      padding: 0 calc((100% - 1520px) / 2);
    }

  }
`;

const NavButton = styled.img.attrs({
  src: "/static/svgs/3seon.svg"
})`
  width: 40px;
  height: 32px;
  margin-left: 30px;

  @media only screen and (min-width: 1280px) {
    display: none;
  }
`;

const Sp = styled.div<NavProps>`
  transition: opacity .3s ease-in-out;
  height: 1px;
  background: #6d788f;
  opacity: ${(props: NavProps) => props.opacity};

  position: relative;
  z-index: -1;
`;

const NavItemStyle = styled.span`
  margin-left: 40px;
  font-size: 12pt;
  text-decoration: none;
  color: black;
  cursor: default;
  display: none;

  @media only screen and (min-width: 1280px) {
    display: inline;
  }

  &::after {
      content: "";
    display: block;
    border-bottom: 5px solid #ffcc66;
    margin: 0 auto;
    width: 0px;
    transition: width .3s ease-in-out;
  }
  &:hover {
    &::after {
      width: 100%;
    }
  }
`;