import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const CurrentNav: React.FC<{
  pages: string[],
  hrefs: string[],
  current: number
}> = ({ pages, hrefs, current }) => {
  return (
    <Bar>
      <div style={{ width: "230px" }}></div>
      {pages.map((v, i) => (
        <React.Fragment key={i}>
          {i === current ? <Bold to={hrefs[i]}>{v}</Bold> : <Page to={hrefs[i]}>{v}</Page>}
          {pages.length !== i + 1 && <>&nbsp;|&nbsp;</>}
        </React.Fragment>
      ))}
    </Bar>
  );
}

const Bar = styled.div`
  display: none;

  @media only screen and (min-width: 1280px) {
    display: block;
    height: 30px;
    background: #f0f1f4;
    padding-left: calc((100% - 1520px) / 2);
    display: flex;
    align-items: center;
  }
`;

const Page = styled(Link)`
  color: #6d788f;
  text-decoration: none;
`;

const Bold = styled(Page)`
  font-family: 'JSDongkang-Bold';
`;

export default CurrentNav;