import React from 'react';
import styled, { createGlobalStyle } from 'styled-components';
import { Route, Switch } from 'react-router-dom';

import Header from '../components/header';
import Footer from '../components/footer';

import * as pages from "../pages";

function App() {
  return (
    <>
      <GlobalStyle />
      <Content>
        <Header />

        <Switch>
          <Route exact path="/" component={pages.Home} />
          <Route exact path="/introduce/pickle" component={pages.Introduce.Pickle} />
          <Route exact path="/introduce/member" component={pages.Introduce.Member} />
          <Route exact path="/introduce/news" component={pages.Introduce.News} />
          <Route exact path="/introduce/cibi" component={pages.Introduce.CIBI} />
          <Route exact path="/introduce/copyright" component={pages.Introduce.Copyright} />
          <Route exact path="/project/covid19" component={pages.Project.Covid19} />
          <Route exact path="/support/help" component={pages.Support.Help} />
          <Route exact path="/support/singo" component={pages.Support.Singo} />
          <Route exact path="/support/notice" component={pages.Support.Notice} />
          <Route exact path="/support/faq" component={pages.Support.FAQ} />
          <Route exact path="/support/terms" component={pages.Support.Terms} />
          <Route exact path="/support/private" component={pages.Support.Private} />
          <Route exact path="/support/refund" component={pages.Support.Refund} />
          <Route component={pages.NotFound} />
        </Switch>
      </Content>

      <Footer />
    </>
  );
}

const GlobalStyle = createGlobalStyle`
  @font-face { font-family: 'JSDongkang-Bold'; src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/JSDongkang-BoldA1.woff') format('woff'); font-weight: normal; font-style: normal; }
  @font-face { font-family: 'JSDongkang-Regular'; src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/JSDongkang-RegularA1.woff') format('woff'); font-weight: normal; font-style: normal; }
  
  body {
    margin: 0;
    height: 100%;
  }
  * {
    font-family: 'JSDongkang-Regular';
  }

  html {
    overflow-y: scroll;
    height: 100%;
  }

  #root {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
`;

const Content = styled.div`
  flex: 1 0 auto;
`;


export default App;
