import React from 'react';
import SubPageWrapper from '../SubPageWrapper';
import styled from 'styled-components';

interface Member {
  name: string;
  short: string;
  description: string[];
  mail: string;
}

const members: Member[] = [
  {
    name: "희망",
    short: "팀 총괄",
    description: ["팀 피클에서 팀장을 맡고 있습니다.", "잘 부탁합니다."],
    mail: "huimang@tpk.kr"
  },
  {
    name: "크레곤",
    short: "개발",
    description: ["팀 피클에서 개발을 맡고 있습니다.", "잘 부탁합니다."],
    mail: "craftydragon678@tpk.kr"
  }
]


const IntroduceMember = () => {
  return (
    <SubPageWrapper navKey="introduce-member">
      {members.map((v, i) => (
        <Card {...v} key={i} />
      ))}
    </SubPageWrapper>
  );
}

const Card: React.FC<Member> = ({ name, short, description, mail }) => {
  return (
    <CardWrapper>
      <InnerCard>
        <div>{name}</div>
        <div>{short}</div>
        <div>{description.map((v, i) => (
          <React.Fragment key={i}>
            {v}
            {i !== description.length - 1 && <br />}
          </React.Fragment>
        ))}</div>
        <div>
          <MailIcon />
          {mail}
        </div>
      </InnerCard>
    </CardWrapper>
  );
}

const CardWrapper = styled.div`
  margin-bottom: 50px;
  background: #ddd;
  width: 100%;
  height: 200px;

  &:hover > div {
    width: 500px;
    & > div {
      &:nth-child(2) {
        opacity: 0;
      }
      &:nth-child(3) {
        opacity: 1;
      }
      &:nth-child(4) {
        opacity: 1;
      }
    }
  }
`;

const InnerCard = styled.div`
  background: #aaa;
  width: 300px;
  height: 200px;
  margin-right: 0;
  margin-left: auto;
  padding: 30px;
  box-sizing: border-box;
  transition: width .3s ease-in-out;

  & > div {
    &:nth-child(1) {
      font-size: 24pt;
      font-family: "JSDongkang-Bold";
      margin-bottom: 10px;
    }
    &:nth-child(2) {
      transition: opacity .3s ease-in-out;
      font-size: 16pt;
      position: absolute;
      opacity: 1;
    }
    &:nth-child(3) {
      transition: opacity .3s ease-in-out;
      opacity: 0;
      margin-bottom: 10px;
    }
    &:nth-child(4) {
      transition: opacity .3s ease-in-out;
      opacity: 0;
    }
  }
`;

const MailIcon = styled.img.attrs({
  src: "/static/svgs/email.svg",
  alt: "email",
  width: 20,
})`
  vertical-align: sub;
  margin-right: 10px;
`;

export default IntroduceMember;