import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const SideNav: React.FC<{
  title: string,
  pages: string[],
  hrefs: string[],
  current: number
}> = ({ title, pages, hrefs, current }) => {
  return (
    <Container>
      <SuperBox>{title}</SuperBox>
      {pages.map((v, i) => (
        <React.Fragment key={i}>
          {i === current ? <SmallBox to={hrefs[i]}>{v}</SmallBox> : <SmallNBox to={hrefs[i]}>{v}</SmallNBox>}
        </React.Fragment>
      ))}
    </Container>
  );
}

const Container = styled.div`
  display: none;

  @media only screen and (min-width: 1280px) {
    display: flex;
    flex-direction: column;
  }
`;

const SuperBox = styled.div`
    background: #ffcc66;
    width: 250px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    color: white;
    font-size: 18pt;

    margin-bottom: 10px;
    margin-right: 50px;
`;

const SmallNBox = styled(Link)`
    background: white;
    width: 250px;
    height: 50px;
    line-height: 50px;
    color: #6d788f;
    font-size: 14pt;
    border-bottom: 1px solid #6d788f;
    box-sizing: border-box;
    padding-left: 16px;
    text-decoration: none;
`;

const SmallBox = styled(SmallNBox)`
    background: #f0f1f4;
    font-family: "JSDongkang-Bold";
`;

export default SideNav;