import React, { useState } from 'react';
import styled from 'styled-components';


interface LanguageProps {
  visible: boolean;
}

const LanguageStyled = styled.div<LanguageProps>`
  display: inline-block;
  position: relative;

  @media only screen and (min-width: 1280px) {
    margin-left: auto;
  }

  & > button {
    margin-left: auto;
    border: 0;
    outline: 0;
    padding: 0px 8px 9px 0px;
    background: transparent;
    margin-right: 30px;

    @media only screen and (min-width: 1280px) {
      border-bottom: 1px solid black;
      margin-right: 0px;
    }
  }
  & > button::before {
    content: "";
    height: 40px;
    width: 40px;
    padding: 50px 0;
    padding-right: 40px;
    background: no-repeat center/40px url("/static/images/language.png");
    background-position: 0px;

    @media only screen and (min-width: 1280px) {
      height: 25px;
      width: 25px;
      background: no-repeat center/19px url("/static/images/language.png");
    }
  }
  & > button::after {
    @media only screen and (min-width: 1280px) {
      content: "Language ▼";
    }
  }
  & > div {
    display: ${(props: LanguageProps) => props.visible ? "block" : "none"};
    position: absolute;
    top: 28px;
    left: 25px;
    min-width: 110px;
    background: white;
    border: 2px solid #6d788f;
    box-sizing: border-box;
  }
  & > div > a {
    display: block;
    text-decoration: none;
    color: black;
    padding: 2.5px 10px;
  }
  & > div > a:hover {
    background: #ffcc66;
  }
`;


const Language = () => {
  const [languageVisible, setLanguageVisible] = useState(false);
  return (
    <LanguageStyled visible={languageVisible} onClick={() => setLanguageVisible(!languageVisible)} onMouseLeave={() => setLanguageVisible(false)}>
      <button></button>
      <div>
        <a href="/">한국어</a>
        <a href="/">English</a>
        <a href="/" style={{ fontFamily: 'sans-serif' }}>日本語</a>
      </div>
    </LanguageStyled>
  );
}

export default Language;